.card {
  width: 272px;
  height: 208px;
  display: flex;
  flex-direction: column;
  align-items: center;
  row-gap: 8px;
  position: relative;
}

.card:hover {
  cursor: grab;
}

.image {
  width: 240px;
  height: 120px;
}

.priceBlock {
  display: flex;
  column-gap: 9px;
}

.text {
  text-align: center;
}